.site{
  &-project{
    &-item{
      background: $white;
      border-radius: 20px;
      margin-bottom: 100px;
      display: flex;
      // align-items: center;
      padding: 40px 40px 0;
      transition: all .3s ease;
      &:hover{
        transform: scale(1.002);
        box-shadow: 0 22px 34px 0 rgba(129,143,151,0.13);
        .site-project-item-content{
          .btn{
            border-color: $text-color;
          }
        }
      }
      &-content{
        width: 60%;
        padding-right: 60px;
        margin-top: 10px;
        span{
          font-size: 13px;
          display: block;
          font-weight: 300;
          text-transform: uppercase;
          letter-spacing: 1px;
        }
        a.title{
          font-size: 30px;
          color: $text-color;
          margin-bottom: 20px;
          font-weight: 300;
          display: inline-block;
          position: relative;
          // &::before{
          //   content: '';
          //   position: absolute;
          //   width: 80%;
          //   height: 8px;
          //   background: $primary-color;
          //   z-index: -1;
          //   left: 0;
          //   bottom: 10px;
          //   transform: scaleX(0);
          //   transform-origin: right;
          //   transition: transform .3s ease-in-out;
          // }
          &:hover{
            text-decoration: none;
            color: $primary-color;
            &::before{
              transform: scaleX(1);
              transform-origin: left;
            }
          }
        }
        p{
          font-weight: 300;
          font-size: 16px;
          margin-bottom: 30px;
          color: $text-color-light;
          line-height: 1.8;
        }
      }
      &-image{
        width: 40%;
        img{
          display: block;
          width: 100%;
          border-radius: 15px 15px 0 0;
        }
      }
      &:nth-child(even){
        .site-project-item-content{
          order: 2;
          padding-right: 0;
          padding-left: 60px;
        }
        .site-project-item-image{
          order: 1;
        }
      }
    }
    &-single{
      &-about{
        margin-top: 250px;
        h3{
          font-weight: 300;
          font-size: 35px;
          margin-bottom: 30px;
          color: $text-color-dark;
        }
        p{
          width: 70%;
          color: #8B99A1;
          font-weight: 300;
          line-height: 28px;
          margin-bottom: 50px;
        }
      }
      &-meta{
        display: flex;
        padding-left: 0;
        margin-bottom: 80px;
        width: 80%;
        li{
          list-style: none;
          flex: 1;
          span{
            display: block;
            &.title{
              margin-bottom: 10px;
              color: $text-color-dark;
            }
            &.info{
              font-weight: 300;
              color: #8B99A1;
              a{
                color: inherit;
                &:hover{
                  color: $primary-color;
                  text-decoration: none;
                }
              }
            }
          }
        }
      }
      &-images{
        .one-col, .two-col{
          .image{
            margin-bottom: 50px;
            overflow: hidden;
            border-radius: 10px;
            transition: all .5s ease;
            img{
              display: block;
              width: 100%;
              transition: inherit;
            }
            &:hover{
              transform: scale(0.99);
              img{
                transform: scale(1.05);
              }
            }
          }
        }
        .two-col{
          display: flex;
          .image{
            width: calc(50% - 25px);
            &:not(:last-child){
              margin-right: 50px;
            }
          }
        }
      }
    }
    &-next{
      background: #E1E3E9;
      text-align: center;
      padding: 200px 0;
      position: relative;
      &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: $primary-color;
        // z-index: -1;
        top: 0;
        left: 0;
        transform: scaleY(0);
        transform-origin: bottom;
        transition: transform .5s ease-in-out;
        // transition-duration: .2s;
      }
      &:hover{
        p{
          span, a{
            color: $white;
          }
        }
        &::before{
          transform: scaleY(1);
          transform-origin: top;
        }
      }
      p{
        position: relative;
        z-index: 1;
        margin-top: 80px;
        margin-bottom: 0;
        &::before{
          position: absolute;
          content: 'Next Project';
          letter-spacing: 5px;
          text-transform: uppercase;
          font-size: 100px;
          color: $text-color-dark;
          left: 50%;
          top: -110px;
          transform: translateX(-50%);
          opacity: 0.3;
        }
        span{
          display: block;
          font-size: 40px;
          margin-bottom: 30px;
          transition: all 0.5s ease;
        }
        a{
          font-size: 14px;
          text-transform: lowercase;
          font-weight: 300;
          letter-spacing: 2px;
          transition: all 0.5s ease;
          position: relative;
          &::before{
            content: '';
            position: absolute;
            width: 110%;
            height: 1px;
            background: $white;
            z-index: -1;
            top: 60%;
            left: -5%;
            transform: scaleX(0);
            transform-origin: right;
            transition: transform .5s ease-in-out;
            transition-duration: .2s;
          }
          &:hover{
            text-decoration: none;
            &::before{
              transform: scaleX(1);
              transform-origin: left;
            }
          }
        }
      }
    }
  }
}