.site-navigation{
  padding:10px 30px;
  background: $body-color;
  z-index: 1000;
  .nav-brand{
    padding: 0;
  }
  .nav-item{
    .nav-link{
      font-size: 18px;
      color: $text-color-dark;
      transition: all .3s ease;
    }
    &:not(:last-child){
      .nav-link{
        padding: 15px 30px;
        &:hover{
          color: $primary-color;
        }
      }
    }
    &:last-child{
      margin-left: 20px;
      .nav-link{
        padding: 15px 30px;
        background: $white;
        border-radius: 6px;
        position: relative;
        overflow: hidden;
        z-index: 1;
        &::before{
          content: '';
          position: absolute;
          width: 100%;
          height: 100%;
          background: $primary-color;
          z-index: -1;
          top: 0;
          left: 0;
          transform: scaleX(0);
          transform-origin: right;
          transition: transform .5s ease-in-out;
          transition-duration: .2s;
        }
        &:hover{
          color: $white;
          &::before{
            transform: scaleX(1);
            transform-origin: left;
          }
        }
      }
    }
  }
}