.site{
  &-cta{
    &-content{
      padding: 50px 0;
      border-top: 1px dashed $text-color-light;
      p{
        font-size: 35px;
        line-height: 1.5;
        margin-bottom: 50px;
        font-weight: 300;
        a{
          color: $primary-color;
          position: relative;
          transition: all .3s ease;
          &::before{
            position: absolute;
            content: '';
            width: 100%;
            height: 2px;
            background: $primary-color;
            bottom: 0;
            transition: all .3s ease;
            z-index: -1;
          }
          &:hover{
            text-decoration: none;
            color: $white;
            &::before{
              height: 100%;
            }
          }
        }
      }
    }
    &-social{
      list-style: none;
      margin: 0; padding: 0;
      li{
        display: inline-block;
        &:not(:last-child){
         margin-right: 30px;
        }
        a{
          color: $text-color;
          font-size: 18px;
          font-weight: 300;
          transition: all .3s ease;
          &:hover{
            text-decoration: none;
            color: #366EFF;
          }
        }
      }
    }
  }
}