.site{
  &-about{
    padding: 300px 0 100px;
    &-content{
      span, p{
        font-size: 35px;
        font-weight: 300;
        line-height: 1.8;
      }
      span{
        color: $text-color-light;
      }
      a{
        color: #366EFF;
        position: relative;
        transition: all .3s ease;
        &::before{
          position: absolute;
          content: '';
          width: 100%;
          height: 2px;
          background: #366EFF;
          bottom: 0;
          transition: all .3s ease;
          z-index: -1;
        }
        &:hover{
          text-decoration: none;
          color: $white;
          &::before{
            height: 100%;
          }
        }
      }
    }
  }
}